	module.exports = {"data":{"markdownRemark":{"html":"<p>Great advice on crafting a home page from <a href=\"http://www.alistapart.com/articles/homepagegoals\">A List Apart</a>. In short: build it last, and work first on the details (the smallest, ubiquitous elements of your site). A great homepage with poor search results or product page will only lead to disappointment.  So if your site is shallow and ugly on the inside, make sure your homepage is too.   Also: </p>\n<blockquote class=\"large\">If a first time visitor to your site's home page does not understand what it is within three seconds, you've failed goal number one, so feel free to skip the rest. The only people who will use the site are the people who already know what it does. Or, ya know, masochists.</blockquote>\n<p>While I'm in a website-improvement mood, there is also a handy post today from Alt Tags, summarizing the <a href=\"http://www.alttags.org/archives/2006/01/08/48/\">Five Steps to a Better Website in the New Year</a> (via the ever-useful <a href=\"http://www.456bereastreet.com/\">456 Bera St.</a>). In short: </p>\n<ul>\n<li>Give Your Content Some Attention</li>\n<li>Validate Your Website's Relevance</li>\n<li>Think About Your Customers</li>\n<li>Review Your Site Navigation</li>\n<li>Identify Accessibility Problems</li>\n</ul>","frontmatter":{"created_at":"January 31, 2006","path":"/2006/01/31/truth-in-a-home-page","title":"Truth in a Home Page ","tags":["webdev","design"]}}},"pathContext":{}}

